import { FaTimes } from "react-icons/fa";
const FormPopup = ({ isOpen, onClose, children }) => {
  return (
    <div className={`form-popup ${isOpen ? "active" : ""}`}>
      <div className="form-popup-center">
        <div className="form-popup-container">
          <a className="form-popup-close-btn" onClick={onClose}>
            <FaTimes size="20" />
          </a>
          {children}
        </div>
      </div>
      <div className="form-popup-bg" onClick={onClose}></div>
    </div>
  );
};
export default FormPopup;
