import { useState } from "react";
import FormDealer from "./FormDealer";
import FormEmail from "./FormEmail";
import FormPopup from "./FormPopup";

import { BlobProvider, pdf } from "@react-pdf/renderer";
import PdfDocument from "../pdf/PdfDocument";

const SummaryButtons = () => {
  const [formPopup, setFormPopup] = useState(false);
  const [form, setForm] = useState("");
  const closePopup = (e) => {
    e.preventDefault();
    setFormPopup(false);
    setForm(null);
  };

  const EmailPopup = (e) => {
    e.preventDefault();
    setFormPopup(true);
    setForm("email");
  };

  const DealerPopup = (e) => {
    e.preventDefault();
    setFormPopup(true);
    setForm("dealer");
  };
  return (
    <BlobProvider document={<PdfDocument />}>
      {({ blob, url, loading, error }) => {
        return loading ? (
          <div>loading pdf...</div>
        ) : (
          <>
            <div className="summary-btns">
              <a className="summary-btn-left" onClick={EmailPopup}>
                Send to My Email
              </a>
              <a className="summary-btn-center" onClick={DealerPopup}>
                Send to Dealer
              </a>
              <a className="summary-btn-right" href={url} download="myboat">
                Download
              </a>
            </div>
            {formPopup ? (
              <FormPopup isOpen={formPopup} onClose={closePopup}>
                {form === "email" ? (
                  <FormEmail blob={blob} />
                ) : (
                  <FormDealer blob={blob} />
                )}
              </FormPopup>
            ) : (
              ""
            )}
          </>
        );
      }}
    </BlobProvider>
  );
};

export default SummaryButtons;
