import {
  Document,
  Page,
  Text,
  View,
  Image,
  StyleSheet,
  Font,
} from "@react-pdf/renderer";
import { connect, Provider } from "react-redux";
import formatMoney from "../helpers/formatMoney";
import { getTotalPrice } from "../state/reducers/choiceReducer";
import store from "../state";

Font.register({
  family: "Oswald",
  src:
    "https://fonts.gstatic.com/s/oswald/v36/TK3_WkUHHAIjg75cFRf3bXL8LICs1xZogUE.ttf",
});

Font.register({
  family: "Montserrat",
  fonts: [
    {
      src:
        "https://fonts.gstatic.com/s/montserrat/v15/JTURjIg1_i6t8kCHKm45_cJD7g4.ttf",
    },
    {
      src:
        "https://fonts.gstatic.com/s/montserrat/v15/JTURjIg1_i6t8kCHKm45_dJE7g4.ttf",
      fontWeight: 700,
    },
  ],
});

const styles = StyleSheet.create({
  body: {
    paddingTop: 35,
    paddingBottom: 65,
    paddingHorizontal: 35,
  },
  heading: {
    fontSize: 14,
    textAlign: "left",
    fontFamily: "Montserrat",
    fontWeight: 700,
    textTransform: "uppercase",
  },
  title: {
    fontSize: 30,
    textAlign: "center",
    fontFamily: "Oswald",
    textTransform: "uppercase",
  },
  row: {
    display: "flex",
    flexDirection: "row",
    flexWrap: "wrap",
    width: "100%",
    padding: 4,
  },
  bordered: {
    borderBottomColor: "#c7c7c7",
    borderBottomWidth: "2",
    borderBottomStyle: "solid",
  },
  column: {
    display: "flex",
    flexDirection: "column",
    flex: 1,
    padding: 4,
  },
  doubleColumn: {
    display: "flex",
    alignItems: "center",
    flexDirection: "column",
    flex: 2,
    padding: 4,
  },
  text: {
    fontSize: 12,
    textAlign: "left",
    fontFamily: "Montserrat",
  },
  price: {
    fontSize: 13,
    fontFamily: "Montserrat",
    fontWeight: "bold",
    color: "#008af4",
    textAlign: "right",
  },
});

const PdfDoc = ({ choices, packaged, total, formData }) => {
  let grouped = {};
  choices.allIds.forEach((id) => {
    const item = choices.byId[id];
    const pageId = item.pageId;
    if (!grouped[pageId]) {
      grouped[pageId] = [item];
    } else {
      grouped[pageId].push(item);
    }
  });
  return (
    <Document>
      <Page size="LETTER" style={styles.body}>
        <Text style={styles.title}>{formData.post.post_title} Summary</Text>
        <View style={{ ...styles.row, ...styles.bordered, padding: 20 }}>
          <View style={styles.column}>
            <Image src={formData.image} />
          </View>
          <View style={styles.column}>
            <Text style={{ ...styles.text, textTransform: "uppercase" }}>
              Total
            </Text>
            <Text
              style={{ ...styles.price, textAlign: "left", fontSize: "30" }}
            >
              ${formatMoney(total(true))}
            </Text>
            <Text
              style={{
                ...styles.price,
                fontWeight: "normal",
                color: "#5d5d5d",
                textAlign: "left",
                fontSize: "17",
              }}
            >
              ${formatMoney(total(false))}
            </Text>
          </View>
        </View>

        {formData.entities.pages.allIds.map((id, index) => {
          const pageItem = formData.entities.pages.byId[id];
          if (grouped.hasOwnProperty(id)) {
            return (
              <View style={{ ...styles.row, ...styles.bordered }}>
                <View style={styles.column}>
                  <Text style={styles.heading}>{pageItem.short}</Text>
                </View>
                <View style={styles.doubleColumn}>
                  {grouped[id].map((item, index) => {
                    const isPackaged = packaged.allIds.includes(item.id);
                    const choiceLabel = (choice) => {
                      if (!choice) return "";
                      return choice.label;
                    };
                    return (
                      <View style={styles.row}>
                        <View style={styles.column}>
                          <View style={{ display: "flex" }}>
                            <Text style={styles.text}>{item.label}</Text>
                            {item.quantity > 1 ? (
                              <Text
                                style={{ ...styles.text, fontWeight: "bold" }}
                              >{` x ${item.quantity}`}</Text>
                            ) : (
                              <></>
                            )}
                          </View>
                        </View>
                        <View style={styles.column}>
                          {isPackaged ? (
                            <Text
                              style={{
                                ...styles.price,
                                fontSize: 8,
                                color: "#000000",
                              }}
                            >
                              Included in{" "}
                              {choiceLabel(
                                choices.byId[packaged.byId[item.id].fromId]
                              )}
                            </Text>
                          ) : item.discount ? (
                            <View>
                              <Text style={styles.price}>
                                ${formatMoney(item.discount)}
                              </Text>
                              <Text
                                style={{
                                  ...styles.price,
                                  fontSize: 8,
                                  fontWeight: "normal",
                                  color: "#5d5d5d",
                                }}
                              >
                                ${formatMoney(item.price)}
                              </Text>
                            </View>
                          ) : (
                            <Text style={styles.price}>
                              ${formatMoney(item.price)}
                            </Text>
                          )}
                        </View>
                      </View>
                    );
                  })}
                </View>
              </View>
            );
          }
        })}
      </Page>
    </Document>
  );
};

const PdfDocConnect = connect((state) => {
  return {
    choices: state.choices,
    packaged: state.packaged,
    total: getTotalPrice(state),
    formData: state.formData,
  };
}, {})(PdfDoc);

const PdfDocWrap = () => (
  <Provider store={store}>
    <PdfDocConnect />
  </Provider>
);

export default PdfDocWrap;
